:root {
    --text-color: rgba(0,0,0,0.77);
    --bg-color: #C8C8CB;
    --accent-color: rgba(91,91,91,0.77);
    --card-color: #F0EBE8;
}

.CardContainer {

    position: absolute;
    box-sizing: border-box;
    justify-content: center;
    align-items: flex-start;
    display: flex;
    background-color: var(--card-color);
    background-image: url("../images/card.jpg");
    top: 5%;
    bottom: 5%;
    left: 5%;
    right: 5%;
    border-radius: 0.5rem;
    box-shadow: 1rem 1rem 1rem rgba(0,0,0,0.69);
}

.CardContainer .CardBorder {
    position: absolute;
    box-sizing: border-box;
    border-style: solid;
    border-radius: 0.5rem;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0.25rem;
    border-color: var(--accent-color);
    border-width: 0.25rem;
}

.CardContainer .CardContainerInner {
    box-sizing: border-box;
    height:inherit;
    padding: 3rem;
    display: flex;
    position: absolute;
    align-items: flex-start;
    flex-direction:column;
    justify-content:space-between;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.NavBar {
    display: inline-flex;
    align-items: flex-start;
    gap: 2rem;
}

.BodyLink {
    font-weight: bold;
    text-decoration-line: none;
    color: var(--text-color);
}

.Footer {
    box-sizing: border-box;
    position: relative;
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

#Socials {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}

.Heading {
    font-weight: normal;
    font-size: 5.5rem;
    color: var(--text-color);
}

#NameTag{
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 0.5rem;
    grid-column: 1;
}

#HRule {
    display: flex;
    height: 4px;
    background-color: var(--accent-color);
    width: 100%;
}

.Content {
    display: inline-grid;
    grid-template-columns: auto,auto;
}

#AboutMe {
    grid-column: 2;
    margin-left: 20%;
}

body {
    font-size: 1.5rem;
    font-family: Courier, monospace;
}


